.search-box {
  position: fixed;
  top: 106px;
  display: none;
  width: 100%;
  z-index: 99;
  background-color: var(--wp--preset--color--spurs-white);
  color: var(--wp--preset--color--spurs-black);
  box-shadow: 0 10px 25px -15px rgba(0, 0, 0, 0.15); }
  .search-box .search-form {
    padding: 1rem 0 2rem;
    position: relative; }
    .search-box .search-form .input-control {
      position: relative; }
      .search-box .search-form .input-control:before {
        content: '\f002';
        font-family: "Font Awesome 5 Pro", sans-serif;
        font-size: 30px;
        font-weight: 400;
        position: absolute;
        top: 10px;
        color: var(--wp--preset--color--spurs-black);
        transform: rotate(90deg);
        /*@include media-breakpoint-down(lg) {
		  top: 12px;
		}*/ }
      .search-box .search-form .input-control .search-input {
        border: 0;
        border-bottom: 2px solid var(--wp--preset--color--spurs-black);
        width: 100%;
        padding: 1rem 1rem 1rem 3rem;
        font-size: 24px;
        background-color: transparent;
        border-radius: 0;
        font-weight: 700; }
        .search-box .search-form .input-control .search-input::placeholder {
          /* Chrome, Firefox, Opera, Safari 10.1+ */
          opacity: 1;
          /* Firefox */ }
        .search-box .search-form .input-control .search-input:-ms-input-placeholder {
          /* Internet Explorer 10-11 */ }
        .search-box .search-form .input-control .search-input::-ms-input-placeholder {
          /* Microsoft Edge */ }
      .search-box .search-form .input-control .search-input ~ .focus-border {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0;
        height: 2px;
        transition: 0.4s; }
      .search-box .search-form .input-control .search-input:focus ~ .focus-border {
        width: 100%;
        transition: 0.4s; }
  .search-box .button-close {
    position: absolute;
    right: 0;
    top: 25px;
    font-size: 25px;
    cursor: pointer; }
  .search-box.active {
    display: inline-block; }

